import { DeepPartial } from '../core'
import { Token, TokenOracle } from './token.model'

export function TokenMock(options?: Partial<Token>): Token {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'TOKEN_ID',

    name: options?.name || 'TOKEN_NAME',
    address: options?.address || 'TOKEN_ADDRESS',
    symbol: options?.symbol || 'SYMB',
    decimals: options?.decimals || 18,

    chainId: options?.chainId || '',

    oracle: options?.oracle,

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
